import Utils from '@/rich-text-editor/utils';
import {TinyEmitter} from 'tiny-emitter';
import ConstVar from '@/rich-text-editor/const';
import {PopperDockType} from '@/rich-text-editor/comp/popper';
// @ts-ignore
import fontPath from '../assets/default.ttf';

interface ICallbackList {
}

interface SelectInfo {
    tag: string,
    el?: Node,
}

export class EditorRange {
    public emitter: TinyEmitter = new TinyEmitter();
    private _contentElement: HTMLElement | null = null;
    private _frame: HTMLIFrameElement | null = null;
    private _callbackList: ICallbackList = {};

    private _record: { startContainer: Node | null, startOffset: number, endContainer: Node | null, endOffset: number } = {
        startContainer: null,
        startOffset: 0,
        endContainer: null,
        endOffset: 0,
    };

    init(el: HTMLElement) {
        el.contentEditable = 'true';
        this._contentElement = el;
    }

    execCommand(cmd: string, value: any = '') {
        const doc = this.getDocument();
        if (doc) {
            doc.execCommand(cmd, false, value);
        }
    }

    public insertHR(color = '#fff', height = 1, margin = 2) {
        const range = this.getRange();
        const doc = this.getDocument();
        if (range && doc) {
            const hr = doc.createElement('hr');
            hr.style.background = color;
            hr.style.height = `${height}px`;
            hr.style.margin = `${margin}px 0`;
            range.insertNode(hr);
        }
    }

    public selectEnd() {
        const range = this.getRange();
        const doc = this.getDocument();
        if (range && doc && this._contentElement) {
            let len = doc.body.childNodes.length;
            range.setStart(this._contentElement, len);
            range.setEnd(this._contentElement, len);
            range.collapse(true);
        }
    }

    private _queryCommand() {
        const link = 'createLink';
        const doc = this.getDocument();
        if (doc) {
            let b = doc.queryCommandEnabled(link);
            let d = doc.queryCommandState(link);
            let e = doc.queryCommandValue(link);
            console.log(111);
            return e;
        }
        return null;
    }

    private getHref(el: Node | undefined | null) {
        if (el && el.nodeName.toLowerCase() === 'a') {
            return (el as HTMLElement).getAttribute('href') || null;
        }
        return null;
    }

    public getSelectContentLink() {
        let ret = null;
        const info = this.getSelectTag();
        if (info && info.el) {
            ret = this.getHref(info.el);
            if (!ret) {
                // fixme 检索href不准确
                ret = this.getHref(info.el.parentElement);
            }
        }
        return ret;
    }

    public setImageSize(width: number, height: number) {
        if (this.isSelectImgTag()) {
            const info = this.getSelectTag();
            if (info) {
                const el = (info.el) as HTMLElement;
                el.setAttribute('width', `${width}px`);
                el.setAttribute('height', `${height}px`);
            }
        }
    }

    private onSelectTag(info: SelectInfo) {

    }

    public isSelectImgTag() {
        const info = this.getSelectTag();
        return !!(info && info.tag === 'img');
    }

    public isSelectLinkTag() {
        const info = this.getSelectTag();
        return !!(info && info.tag === 'a');
    }

    public getSelectTag(): SelectInfo | null {
        const ret: SelectInfo = { tag: 'text' };
        const range = this.getRange();
        if (range && !range.collapsed) {
            if (range.startContainer === range.endContainer) {
                // 选中的是一段range
                if (this.rangeStartIsType()) {
                    ret.el = range.startContainer;
                    return ret;
                } else {
                    let node = range.commonAncestorContainer.childNodes[range.startOffset];
                    if (node) {
                        ret.tag = node.nodeName.toLowerCase();
                        ret.el = node;
                        return ret;
                    }
                }
            } else {
                // 选中的是多段range
                const bothText = this.rangeStartIsType() && this.rangeEndIsType();
                if (bothText) {
                    return ret;
                } else {
                    const el = range.startContainer.parentElement;
                    if (el) {
                        ret.tag = el.nodeName.toLowerCase();
                        ret.el = el;
                        return ret;
                    }
                }
            }
        }
        return null;
    }

    public getPopperPositionBySelect() {
        const rect = this.getSelectRect();
        if (!rect) return { x: 0, y: 0 };
        let frameX = 0;
        let frameY = 0;
        let scrollLeft = 0;
        let scrollTop = 0;
        if (this._frame) {
            const frameRect = this._frame.getBoundingClientRect();
            let style = getComputedStyle(this._frame);
            frameX = frameRect.x;
            frameY = frameRect.y;
            scrollLeft = this._frame.contentDocument?.body.scrollLeft || 0;
            scrollTop = this._frame.contentDocument?.body.scrollTop || 0;
        }
        const x = rect.x + frameX + scrollLeft;
        let y = rect.y + rect.height + frameY;
        let type = PopperDockType.Bottom;
        if (y < (document.body.clientHeight / 2)) {
            // dock bottom
            type = PopperDockType.Bottom;
            return { x, y, type };
        } else {
            // dock top
            y = rect.y + frameY;
            type = PopperDockType.Top;
            return { x, y, type };
        }
    }

    private _insertBr(num = 1) {
        const range = this.getRange();
        const doc = this.getDocument();
        if (range && doc) {
            for (let i = 1; i <= num; i++) {
                const br = doc.createElement('br');
                range.insertNode(br);
                range.insertNode(doc.createTextNode('&nbsp'));
                if (num === i) {
                    return br;
                }
            }
        }
        return null;
    }

    // 测试逻辑
    private insertBR() {
        console.log('insertBR');
        const doc = this.getDocument();
        const range = this.getRange();
        if (doc && range) {
            if (this.rangeEndIsType()) {
                // 是否处于文档的尾部
                if (range.endContainer === doc.body.lastChild) {
                    const total = range.endContainer.nodeValue!.length;
                    const current = range.endOffset;
                    if (total === current) {
                        // 在text末尾换行
                        const br = this._insertBr(2);
                        // 如果是整个富文本的末尾，插入2个br，并选中第2个br的开头
                        if (br) {
                            const childNodes = br.parentElement?.childNodes;
                            if (childNodes) {
                                for (let i = 0; i < childNodes.length; i++) {
                                    if (br === childNodes[i]) {
                                        range.setStart(br.parentElement!, i + 1);
                                        range.setStart(br.parentElement!, i + 1);
                                        range.collapse(true);
                                    }
                                }
                            }

                        }

                    } else {
                        // 一段text从中间被分开了
                        const br = this._insertBr(1);
                        // 选中br后下一行被分开的text，offset=0
                        if (br) {
                            const childNodes = br.parentElement?.childNodes;
                            if (childNodes) {
                                for (let i = 0; i < childNodes.length; i++) {
                                    if (childNodes[i] === br) {
                                        let next = null;
                                        let nextIndex = i + 1;
                                        if (nextIndex < childNodes.length) {
                                            next = childNodes[nextIndex];
                                        }
                                        if (next) {
                                            range.setStart(next, 0);
                                            range.setEnd(next, 0);
                                            range.collapse(true);
                                        }
                                    }
                                }
                            }
                        }
                    }
                } else {
                    this._insertBr(1);
                }
            } else {
                // 如果后边有br，就不需要再插入第二个br
                this._insertBr(1);
            }
        }
    }

    private _initEvent() {
        if (this._contentElement) {
            this._contentElement.onkeydown = (event: KeyboardEvent) => {
                event.stopImmediatePropagation();
                event.stopPropagation();
                if (event.keyCode === 13) { // 回车键
                    this.execCommand('insertHTML', '<br>&nbsp');
                    this.execCommand('delete');
                    this.focus();
                    return false;
                }
            };
            this._contentElement.onmouseup = (event: MouseEvent) => {
                setTimeout(() => {
                    const info = this.getSelectTag();
                    if (info) {
                        this.emitter.emit(ConstVar.Msg.UpdateCommandState);
                        this.onSelectTag(info);
                    }
                });
            };
        }
    }

    private _watchChange() {
        // let observer = new MutationObserver((mutations: MutationRecord[], observer: MutationObserver) => {
        //   console.log('change');
        // });
        // observer.observe(editorEl, {
        //   attributes: true,
        //   childList: true,
        //   subtree: true,
        // });
    }

    getDocument() {
        return this._frame?.contentDocument || null;
    }

    private _setDefaultFont() {
        const doc = this.getDocument();
        if (doc) {
            const style = doc.createElement('style');
            const fontName = 'game-font';
            style.innerHTML = `
            @font-face{
                font-family:${fontName};
                src:url('${fontPath}');
            }
            body{
                font-family:${fontName};
            }
            `;
            doc.head.appendChild(style);
        }
    }

    setBgColor(color: string) {
        const doc = this.getDocument();
        if (doc) {
            doc.body.style.backgroundColor = color;
        }
    }

    initWithFrame(frame: HTMLIFrameElement, opts?: ICallbackList) {
        if (opts) {
            this._callbackList = opts;
        }
        this._frame = frame;
        return new Promise((resolve, reject) => {
            frame.onload = () => {
                const body = frame.contentDocument?.body;
                if (body) {
                    body.contentEditable = 'true';
                    this._contentElement = body;
                    this._setDefaultFont();
                }
                this._initEvent();
                resolve(0);
            };
        });
    }

    setInnerHtml(html: string) {
        if (this._contentElement) {
            this._contentElement.innerHTML = html;
        }
    }

    setBaseFontSize(size: number) {
        if (this._contentElement) {
            this._contentElement.style.fontSize = `${size}px`;
        }
    }

    getBaseFontSize() {
        if (this._contentElement) {
            return Utils.getElementFontSize(this._contentElement);
        } else {
            return null;
        }
    }

    getInnerHtml(bExport = false) {
        const size = this.getBaseFontSize();
        let innerHtml = this._contentElement?.innerHTML || '';
        // innerHtml = '<br/>55<font color="#61b46c">66</font>';
        // innerHtml = '<span style="color:#61b46c;">66</span>';// 测试span标签color
        // innerHtml = '<span style="color:#61b46c;font-size:96px;">66</span>';// 测试span标签font-size
        // innerHtml = '<font style="text-decoration-line: line-through; font-size: 30px;">1</font>';// 测试删除线写在样式里面
        // innerHtml = '<font style="text-decoration-line: line-through;font-style:italic; font-size: 30px;">1</font>';// 测试倾斜写在样式里面
        // return  innerHtml = '<font size="100"><font style="font-size: 38px;"><font>23</font><font color="#f71203">49</font></font></font></font>'; // 测试font空属性，导致font-size异常的bug
        // innerHtml = '<font color="#ffff00"><br></font><font>white</font>';
        // innerHtml = ' 123<hr style="background: rgb(255, 255, 255); height: 1px; margin: 2px 0px;"/>33 '; // 测试hr能否变为<hr/>
        // innerHtml = '1<br>2<br>3';
        if (bExport) {
            return `<font>${innerHtml}</font>`;
        } else {
            return `<font size="${size}">${innerHtml}</font>`;
        }
    }

    recordRange() {
        const range = this.getRange();
        if (range) {
            this._record.startContainer = range.startContainer;
            this._record.startOffset = range.startOffset;
            this._record.endContainer = range.endContainer;
            this._record.endOffset = range.endOffset;
        }
    }

    recoverRange() {
        const range = this.getRange();
        const { startOffset, startContainer, endOffset, endContainer } = this._record;
        if (range && startContainer && endContainer) {
            range.setStart(startContainer, startOffset);
            range.setEnd(endContainer, endOffset);
        }
    }

    private getSelection() {
        if (this._frame) {
            return this._frame.contentWindow?.getSelection();
        }
        return null;
    }

    private getRange(): Range | null {
        const selection = this.getSelection();
        if (selection) {
            if (selection.rangeCount) {
                return selection.getRangeAt(0);
            } else {
                const range = document.createRange();
                selection.addRange(range);
                return range;
            }
        }
        return null;
    }

    get isSelectEmpty() {
        const range = this.getRange();
        return !!(range && range.collapsed);
    }

    rangeStartIsType(type: number = Node.TEXT_NODE) {
        const range = this.getRange();
        if (range) {
            if (type === range.startContainer.nodeType) {
                return true;
            }
        }
        return false;
    }

    rangeEndIsType(type: number = Node.TEXT_NODE) {
        const range = this.getRange();
        if (range) {
            if (type === range.endContainer.nodeType) {
                return true;
            }
        }
        return false;
    }

    getSelectRect(): DOMRect | null {
        const range = this.getRange();
        if (range && !range.collapsed) {
            return range.getBoundingClientRect();
        }
        return null;
    }

    getSelectElement(): HTMLElement | null {
        const range = this.getRange();
        if (range && !range.collapsed) {
            if (this.rangeStartIsType()) {
                return range.startContainer as HTMLElement;
            } else {
                const list = range.commonAncestorContainer.childNodes;
                const elStart: HTMLElement = list[range.startOffset] as HTMLElement;
                return elStart;
            }
        }
        return null;
    }

    public isApplyCommandEffect(cmd: string): boolean {
        const doc = this.getDocument();
        if (doc) {
            const enabled = doc.queryCommandEnabled(cmd); // 检测命令是否可以使用
            const state = doc.queryCommandState(cmd);// 检测命令是否已经应用
            const value = doc.queryCommandValue(cmd);// 获取命令传入的值
            return enabled && state;
        }
        return false;
    }

    public getCommandEffectValue(cmd: string): string {
        const doc = this.getDocument();
        if (doc) {
            return doc.queryCommandValue(cmd);
        }
        return '';
    }

    setSelectFontSize(size: number) {
        if (this.isSelectEmpty) {
            console.log('select empty');
        } else {
            const range = this.getRange();
            if (range) {
                let font: HTMLElement | null = null;
                if (range.startContainer === range.endContainer) {
                    if (this.rangeStartIsType()) {
                        // 如果startContainer是文本、注释、CDATA区块，startOffset是字符数量
                        font = document.createElement('font');
                        font.draggable = false;
                        range.insertNode(font);
                        range.surroundContents(font);// 不能将2个片段分割，否则会抛出异常
                    } else {
                        // startOffset 子节点数量
                        const list = range.commonAncestorContainer.childNodes;
                        const elStart: HTMLElement = list[range.startOffset] as HTMLElement;
                        const elEnd: HTMLElement = list[range.endOffset] as HTMLElement;
                        if (Node.ELEMENT_NODE === elStart.nodeType && elStart.nodeName.toLowerCase() === 'font') {
                            font = elStart;
                        }
                    }
                } else {
                    // 跨range选择了
                    console.log('un support');
                    const fragment = range.cloneContents();
                    this._cleanFontSize(fragment);
                    // 空font的合并
                    range.deleteContents();

                    font = document.createElement('font');
                    font.draggable = false;
                    // 里面的font统一size

                    font.appendChild(fragment);
                    range.insertNode(font);
                }

                if (font) {
                    font.style.fontSize = `${size}px`;
                }
            }
        }
    }

    private _cleanFontSize(frag: Node) {
        if (frag.nodeName.toLowerCase() === 'font') {
            (frag as HTMLElement).style.removeProperty('font-size');
        }
        frag.childNodes.forEach(node => {
            this._cleanFontSize(node);
        });
    }

    public focus() {
        this._contentElement?.focus();
    }

    selectAll() {
        const range = this.getRange();
        if (range && this._contentElement && this._contentElement.firstChild && this._contentElement.lastChild) {
            range.setStart(this._contentElement.firstChild, 0);
            range.setEnd(this._contentElement.lastChild, 0);
        }
    }

    test() {
        const range = this.getRange();
        if (range) {
            // 起点结束点是否在一起
            if (!range.collapsed) {
                let node = document.getElementsByClassName('editor')[0];
                if (node) {
                    range.setStart(node, 1);
                    // range.setEnd(node, 2);
                }

                let docFragment = range.cloneContents();
                let div = document.createElement('div');
                div.appendChild(docFragment);
                let selectHTML = div.innerHTML;
                console.log(selectHTML);
                return;
            }
        }

    }
}

const EditorRangeInstance = new EditorRange();
export default EditorRangeInstance;
