import Utils from '@/rich-text-editor/utils';
import {AttributeTranslationConfig, getStyle, KeysFontSize, setStyle, Tags} from './const';
import Base64 from '@/rich-text-editor/base64';

const { TagHr, TagItalic, TagA, TagSpan, TagBold, TagDelete, TagFont, TagImage, TagUnderline } = Tags;
export const Engine2Editor: Record<string, Array<AttributeTranslationConfig>> = {};
Engine2Editor[TagHr] = [
    {
        key: 'color',
        del: true,
        handle(property: Record<string, any>, value: any) {
            // 将color='#ffffff'展开到style='background:"white"'
            const oldStyle = getStyle(property);
            setStyle(property, [`background:${value}`]);
        },
    },
    {
        key: 'height',
        del: true,
        handle(property: Record<string, any>, value: any) {
            setStyle(property, [`height:${Utils.fillTail(value, 'px')}`]);
        },
    },
    {
        key: 'margin',
        del: true,
        handle(property: Record<string, any>, value: any) {
            setStyle(property, [`margin:${Utils.fillTail(value, 'px')} 0;`]);
        },
    },
];
Engine2Editor[TagUnderline] = [
    {
        key: 'color',
        handle(property: Record<string, any>, value: any) {
            setStyle(property, [`color:${value}`]);
        },
    },
];
Engine2Editor[TagItalic] = [
    {
        key: 'color',
        handle(property: Record<string, any>, value: any) {
            setStyle(property, [`color:${value}`]);
        },
    },
];
Engine2Editor[TagDelete] = [
    {
        key: 'color',
        handle(property: Record<string, any>, value: any) {
            setStyle(property, [`color:${value}`]);
        },
    },
];
Engine2Editor[TagBold] = [
    {
        key: 'color',
        handle(property: Record<string, any>, value: any) {
            setStyle(property, [`color:${value}`]);
        },
    },
];
Engine2Editor[TagA] = [
    {
        key: 'color',
        handle(property: Record<string, any>, value: any) {
            setStyle(property, [`color:${value}`]);
        },
    },
];
Engine2Editor[TagSpan] = [
    {
        key: '',

    },
];
Engine2Editor[TagFont] = [
    {
        key: 'size',
        del: true,
        handle(property: Record<string, any>, value: string) {
            // 将size="1"展开到style='font-size:1px';
            const oldStyle = getStyle(property);
            if (oldStyle) {
                // 重新对value进行修改
                const allCss = oldStyle.split(';');
                let newAllCss: string[] = [];
                allCss.forEach((css: string) => {
                    let [key, val] = css.split(':');
                    if (key === KeysFontSize) {
                        val = Utils.fillTail(value, 'px');
                    }
                    newAllCss.push(`${key}:${val}`);
                });
                setStyle(property, newAllCss);
            } else {
                const val = Utils.fillTail(value, 'px');
                setStyle(property, [`${KeysFontSize}:${val}`]);
            }
        },
    },
];

Engine2Editor[TagImage] = [
    {
        key: 'type',
        del: true,
    },
    {
        key: 'src',
        format(val: string) {
            return Base64.fillImageHead(val);
        },
    },
];
