import Utils from '@/rich-text-editor/utils';
import {colorToHex} from '@/rich-text-editor/util-color';
import {AttributeParser, AttributeTranslationConfig, KeysFontSize, Tags, xmlAttributeKey} from '@/rich-text-editor/translate/const';
import lodash from 'lodash';
import Base64 from '@/rich-text-editor/base64';

const { TagHr, TagItalic, TagA, TagSpan, TagBold, TagDelete, TagFont, TagImage, TagUnderline } = Tags;

function updateProperty(property: Record<string, string>, key: string, value: string) {
    const sizeKey = xmlAttributeKey(key);
    property[sizeKey] = value;
}

export const Editor2Engine: Record<string, Array<AttributeTranslationConfig>> = {};
Editor2Engine[TagBold] = [
    {
        key: 'style',
        del: true,
        handle(property: Record<string, any>, value: string) {
            const allCss = Utils.parseStyle(value);
            const color = allCss['color'];
            if (color) {
                updateProperty(property, 'color', colorToHex(color));
            }
        },
    },
];
Editor2Engine[TagUnderline] = [
    {
        key: 'style',
        del: true,
        handle(property: Record<string, any>, value: string) {
            const allCss = Utils.parseStyle(value);
            const color = allCss['color'];
            if (color) {
                updateProperty(property, 'color', colorToHex(color));
            }
        },
    },
];
Editor2Engine[TagItalic] = [
    {
        key: 'style',
        del: true,
        handle(property: Record<string, any>, value: string) {
            const allCss = Utils.parseStyle(value);
            const color = allCss['color'];
            if (color) {
                updateProperty(property, 'color', colorToHex(color));
            }
        },
    },
];
Editor2Engine[TagDelete] = [
    {
        key: 'style',
        del: true,
        handle(property: Record<string, any>, value: string) {
            const allCss = Utils.parseStyle(value);
            const color = allCss['color'];
            if (color) {
                updateProperty(property, 'color', colorToHex(color));
            }
        },
    },
];
Editor2Engine[TagHr] = [
    {
        key: 'style',
        del: true,
        handle(property: Record<string, any>, value: string) {
            const allCss = Utils.parseStyle(value);
            const bgColor = allCss['background'];
            if (bgColor) {
                const color = colorToHex(bgColor);
                updateProperty(property, 'color', color);

            }
            const height = allCss['height'] || '1';
            if (height) {
                updateProperty(property, 'height', Utils.subTail(height, 'px', true));
            }
            const margin = allCss['margin'];
            if (margin) {
                const v = Utils.subTail(margin.split(' ')[0], 'px', true);
                updateProperty(property, 'margin', v);
            }
        },
    },
];
Editor2Engine[TagA] = [
    {
        key: 'style',
        del: true,
        handle(property: Record<string, any>, value: string) {
            const allCss = Utils.parseStyle(value);
            const color = allCss['color'];
            if (color) {
                updateProperty(property, 'color', colorToHex(color));
            }
        },
    },
];
// todo width height
Editor2Engine[TagImage] = [
    {
        key: 'src',
        async format(property: Record<string, any>, val: string) {
            const attrParser = new AttributeParser(property);
            const width = attrParser.parser('width')
                .toInt();
            const height = attrParser.parser('height')
                .toInt();
            // base64压缩
            if (width && height) {
                val = await Base64.compressBase64(val, width, height);
            }
            return Base64.cutImageHead(val);
        },
        handle(property: Record<string, any>, value: string) {
            updateProperty(property, 'type', '2');// base64
        },
    },
    {
        key: 'width',
        format(property: Record<string, any>, val: string) {
            return Utils.subTail(val, 'px', true);
        },
    },
    {
        key: 'height',
        format(property: Record<string, any>, val: string) {
            return Utils.subTail(val, 'px', true);
        },
    },
];
// 会出现span的标签问题
Editor2Engine[TagSpan] = [
    {
        key: 'style',
        del: true,
        handle(property: Record<string, any>, value: string) {
            const allCss = Utils.parseStyle(value);
            const color = allCss['color'];
            if (color) {
                const c = colorToHex(color);
                updateProperty(property, 'color', c);
            }
            let fontSize = allCss['font-size'];
            if (fontSize) {
                fontSize = Utils.subTail(fontSize, 'px', true);
                updateProperty(property, 'size', fontSize);
            }
        },
    },
];
Editor2Engine[TagFont] = [
    {
        key: 'size',
        format(property: Record<string, any>, val: any) {
            if (typeof val === 'string') {
                return Utils.subTail(val, 'px');
            }
        },
    },
    {
        key: 'draggable',
        del: true,
    },
    {
        key: 'style',
        del: true,
        handle(property: Record<string, any>, value: string) {
            const allCss = Utils.parseStyle(value);
            const fontSize = allCss[KeysFontSize];
            if (fontSize) {
                const v = Utils.subTail(fontSize, 'px', true);
                updateProperty(property, 'size', v);
            }
            const color = allCss['color'];
            if (color) {
                const c = colorToHex(color);
                updateProperty(property, 'color', c);
            }
        },
        assembler(arr: Array<any>, value: string) {
            // <font style="text-decoration-line:line-through;"></font>增加一层del
            const allCss = Utils.parseStyle(value);
            const delLine = allCss['text-decoration-line'];
            if (delLine === 'line-through') {
                const newArray = {
                    del: lodash.clone(arr),
                };
                arr.length = 0;
                arr.push(newArray);
            }
            // <font style="font-style:italic;"></font>
            const fontStyle = allCss['font-style'];
            if ('italic' === fontStyle) {
                const newArray = {
                    i: lodash.clone(arr),
                };
                arr.length = 0;
                arr.push(newArray);
            }
        },
    },
];
