export const Tags = {
    TagFont: 'font',
    TagSpan: 'span',
    TagA: 'a',
    TagImage: 'img',
    TagHr: 'hr',
    TagBold: 'b',
    TagDelete: 'del',
    TagUnderline: 'u',
    TagItalic: 'i',
};
export const KeysFontSize = 'font-size';

export class AttributeParser {
    private data: any;
    private curValue = '';

    constructor(data: any) {
        this.data = data;
    }

    parser(attr: string) {
        const key = xmlAttributeKey(attr);
        this.curValue = this.data[key];
        return this;
    }

    isValid(attr: string) {
        const key = xmlAttributeKey(attr);
        // eslint-disable-next-line no-prototype-builtins
        if (this.data.hasOwnProperty(key)) {
            return true;
        }
        return false;
    }

    toString() {
        return this.curValue || '';
    }

    toBool() {
        return this.curValue === 'true';
    }

    toInt() {
        return parseInt(this.curValue) || 0;
    }
}
export interface AttributeTranslationConfig {
    key: string,
    del?: boolean,
    // 对数据进行格式化处理
    // 比如: 20px -> 20
    format?: Function,
    // 将style的具体样式变为tag的属性
    // 比如： <font style="font-size:20px"></font>
    // 转换为：<font size="20"></font>
    handle?: Function,

    // 将tag进行重新的组装
    // 比如：<font style="font-size:20px;text-decoration-line:line-through;">123</font>
    // 转换为：<font size="20"><del>123<del></font>
    assembler?:Function,
}

export function xmlAttributeKey(attr: string):string {
    return `@_${attr}`;
}

export function getStyle(property: Record<string, any>) {
    const styleKey = xmlAttributeKey('style');
    return property[styleKey];
}

export function setStyle(property: Record<string, any>, css: string[]) {
    const styleKey = xmlAttributeKey('style');
    const v = property[styleKey];
    if (v) {
        // 过滤重复key
        const arr = v.split(';');
        css = arr.concat(css);
    }
    property[styleKey] = css.join(';');
}
