import {defineStore} from 'pinia';

export interface IPopper {
    x: number,
    y: number,
    url: string,
    widthOrigin: number,
    heightOrigin: number,
    width: number,
    height: number,
}

const popperStore = defineStore('popper', {
    state: (): IPopper => {
        return {
            x: 0,
            y: 0,
            url: '',
            widthOrigin: 0,
            heightOrigin: 0,
            width: 0,
            height: 0,
        };
    },
});
export default popperStore;