export const ImageTypes = [{ ext: 'png', head: 'iVBORw0KGgoAAAA' }, { ext: 'jpeg', head: '/9j/4Q' }];

const Base64 = {
    _fillHead(ext: string) {
        return `data:image/${ext};base64,`;
    },
    fillImageHead(val:string) {
        for (let i = 0; i < ImageTypes.length; i++) {
            const { ext, head } = ImageTypes[i];
            if (val.startsWith(head)) {
                return this._fillHead(ext) + val;
            }
        }
        return val;
    },
    cutImageHead(val: string) {
        const ext = this.getImageExt(val);
        if (ext) {
            const flag = this._fillHead(ext);
            if (val.startsWith(flag)) {
                return val.substring(flag.length, val.length);
            }
        }
        return val;
    },
    getImageExt(val: string): string | null {
        for (let i = 0; i < ImageTypes.length; i++) {
            const { ext, head } = ImageTypes[i];
            const flag = this._fillHead(ext);
            if (val.startsWith(flag)) {
                return ext;
            }
        }
        return null;
    },
    async getImageSize(base64: string): Promise<{ width: number, height: number }> {
        return new Promise((resolve, reject) => {
            const img = new Image();
            img.src = base64;
            img.onload = () => {
                resolve({ width: img.width, height: img.height });
            };
        });
    },
    async compressBase64(base64: string,  width: number, height: number): Promise<string> {
        return new Promise((resolve, reject) => {
            let img = new Image();
            img.src = base64;
            img.onload = () => {
                let canvas = document.createElement('canvas');
                if (width >= img.width && height >= img.height) {
                    // 如果压缩的尺寸比原图还要大，直接使用原图的数据源
                    resolve(base64);
                } else {
                    canvas.width = width;
                    canvas.height = height;
                    canvas.setAttribute('width', `${width}px`);
                    canvas.setAttribute('height', `${height}px`);
                    let ctx = canvas.getContext('2d')!;
                    ctx.clearRect(0, 0, width, height);
                    ctx.drawImage(img, 0, 0, width, height);
                    let quality = 0.9;
                    const ext = this.getImageExt(base64);
                    let compressData = canvas.toDataURL(`image/${ext}`, quality);
                    console.log('base64:', compressData);
                    resolve(compressData);
                }
            };
        });
    },
};
export default Base64;