import {createPopper, top} from '@popperjs/core';
import TLink from '@/rich-text-editor/view/link.vue';
import {defineComponent, onMounted, provide, ref, createVNode, render, createElementVNode} from 'vue';
import EditorRangeInstance from '@/rich-text-editor/EditorRange';

const Utils = {
    loadFont(name: string, url: string) {
        let style = document.createElement('style');
        style.type = 'text/css';
        style.innerText = `@font-face {font-family: ${name}; src:url('${url}')}`;
        document.head.appendChild(style);

    },
    parseStyle(value: string): Record<string, string> {
        const ret: Record<string, string> = {};
        let allCss = value.split(';');
        allCss.forEach(style => {
            if (style.indexOf(':') !== -1) {
                let [key, value] = style.split(':');
                key = key.trim();
                value = value.trim();
                if (key) {
                    ret[key] = value;
                }
            }
        });
        return ret;
    },
    seepFrameEvent() {
        const iframes = document.getElementsByTagName('iframe');
        for (let i = 0; i < iframes.length; i++) {
            const item = iframes.item(i);
            if (item && item.contentWindow) {
                item.contentWindow.addEventListener('load', () => {
                    ['mousedown', 'click'].map((type) => {
                        if (item.contentDocument) {
                            // @ts-ignore
                            item.contentDocument.addEventListener(type, (event: MouseEvent) => {
                                const e = document.createEvent('MouseEvent');
                                e.initEvent(event.type);
                                document.dispatchEvent(e);
                            });
                        }
                    });
                });
            } else {
                console.log('failed');
            }
        }
    },
    getElementFontSize(el: HTMLElement) {
        let size = getComputedStyle(el).fontSize;
        if (size) {
            return this.subTail(size, 'px', true);
        } else {
            return null;
        }
    },
    subTail(str: string, tail: string, trim = false) {
        let ret = str;
        if (str.endsWith(tail)) {
            ret = str.substring(0, str.length - tail.length);
        }
        if (trim) {
            ret = ret.trim();
        }
        return ret;
    },
    fillTail(str: string, tail: string) {
        if (!str.endsWith(tail)) {
            return str + tail;
        }
        return str;
    },
};
export default Utils;