import {X2jOptionsOptional, XMLBuilder, XmlBuilderOptionsOptional, XMLParser} from 'fast-xml-parser';
import Utils from '../utils';
import lodash from 'lodash';
import chalk from 'chalk';
import {colorToHex} from '@/rich-text-editor/util-color';
import appStore from '@/rich-text-editor/store';
import EditorRangeInstance, {EditorRange} from '@/rich-text-editor/EditorRange';
import {AttributeParser, AttributeTranslationConfig, Tags, xmlAttributeKey} from '@/rich-text-editor/translate/const';
import {Editor2Engine} from '@/rich-text-editor/translate/editor2engine';
import {Engine2Editor} from '@/rich-text-editor/translate/engine2editor';

enum TagSwitchType {
    BothSidesSwap,
    Editor2Engine,
    Engine2Editor,
}

interface TagSwitch {
    editor: string,
    engine: string,
    type: TagSwitchType,
}

const TagSwitchKeys: Array<TagSwitch> = [
    { editor: 'strike', engine: 'del', type: TagSwitchType.BothSidesSwap },
    { editor: 'span', engine: 'font', type: TagSwitchType.Editor2Engine },
];
const preserveOrder = true;
const ignoreAttributes = false;
const trimValues = false;
const showLog = true;
const suppressUnpairedNode = true;

const AttributeFlag = ':@';
const parserOptions: X2jOptionsOptional = {
    preserveOrder, ignoreAttributes,
    unpairedTags: ['br', 'hr'], // 未成对出现的标签
    trimValues,
    parseTagValue: false,
    allowBooleanAttributes: true,
    stopNodes: ['*.hr', '*.br'],
};
const builderOptions: XmlBuilderOptionsOptional = {
    preserveOrder, ignoreAttributes,
    format: false,
    suppressUnpairedNode,
    suppressEmptyNode: true,
    suppressBooleanAttributes: true,
};

export class XmlTranslate {
    private transformKey: Record<string, string> = {};
    private attributeCfg: Record<string, Array<AttributeTranslationConfig>> = {};
    private _valueCheck: ((v: any) => any) = (val: any) => val;

    private _init(editor2Engine: boolean) {
        this._confirmTransformKey(editor2Engine);
        this._initValueCheckFunction(editor2Engine);
    }

    private _initValueCheckFunction(editor2Engine: boolean) {
        if (editor2Engine) {
        } else {
            this._valueCheck = (val: any) => {
                return val;
                // if (typeof val === 'string') {
                //     return val.replace(/ /g, '&nbsp;');
                // } else {
                //     return val;
                // }
            };
        }
    }

    private _confirmTransformKey(editor2engine: boolean) {
        if (editor2engine) {
            this.attributeCfg = Editor2Engine;
            TagSwitchKeys.forEach((item) => {
                if (item.type === TagSwitchType.BothSidesSwap || item.type === TagSwitchType.Editor2Engine) {
                    this.transformKey[item.editor] = item.engine;
                }
            });
        } else {
            this.attributeCfg = Engine2Editor;
            TagSwitchKeys.forEach((item) => {
                if (item.type === TagSwitchType.BothSidesSwap || item.type === TagSwitchType.Engine2Editor) {
                    this.transformKey[item.engine] = item.editor;
                }
            });
        }
    }

    private async checkProperty(tag: string, item: any) {
        const cfgArray = this.attributeCfg[tag];
        if (cfgArray) {
            for (let i = 0; i < cfgArray.length; i++) {
                const cfg = cfgArray[i];
                const property = item[AttributeFlag];
                const key = xmlAttributeKey(cfg.key);
                if (cfg.assembler && property && property[key]) {
                    cfg.assembler(item[tag], property[key]);
                }
                if (cfg.handle && property && property[key]) {
                    cfg.handle(property, property[key]);
                }
                if (cfg.format && property && property[key]) {
                    property[key] = await cfg.format(property, property[key]);
                }
                if (cfg.del === true && property) {
                    delete property[key];
                }
            }
        }
    }


    private async cloneVar(ret: Record<string, any> | Array<any>, data: Record<string, any> | Array<any>) {
        if (lodash.isArray(data) && lodash.isArray(ret)) {
            for (let i = 0; i < (data as Array<any>).length; i++) {
                let item: any = data[i];
                if (lodash.isArray(item)) {
                    let val: Array<any> = [];
                    await this.cloneVar(val, item);
                    ret.push(val);
                } else if (lodash.isObject(item)) {
                    const keys = Object.keys(item);
                    for (let index = 0; index < keys.length; index++) {
                        const key = keys[index];
                        await this.checkProperty(key, item);
                    }
                    let val = {};
                    await this.cloneVar(val, item);
                    ret.push(val);
                } else {
                    ret.push(item);
                }
            }
        } else if (lodash.isObject(data) && lodash.isObject(ret)) {
            for (let key in data) {
                // @ts-ignore
                let item: any = data[key];
                if (typeof item === 'string' || typeof item === 'number' || typeof item === 'boolean') {
                    // @ts-ignore
                    ret[key] = this._valueCheck(item);
                } else if (Array.isArray(item)) {
                    let newKey = key;
                    if (this.transformKey[key]) {
                        newKey = this.transformKey[key];
                    }
                    // @ts-ignore
                    ret[newKey] = [];
                    // @ts-ignore
                    await this.cloneVar(ret[newKey], data[key]);
                } else if (lodash.isObject(item)) {
                    // @ts-ignore
                    ret[key] = {};
                    // @ts-ignore
                    await this.cloneVar(ret[key], item);
                }
            }
        }
    }

    getSettingsXML(bExport: boolean) {
        const store = appStore();
        let attr: Record<string, string> = {};
        if (!bExport) {
            attr[xmlAttributeKey('fixSize')] = store.fixSize.toString();
            attr[xmlAttributeKey('fixWidth')] = store.fixWidth.toString();
            attr[xmlAttributeKey('fixHeight')] = store.fixHeight.toString();
            attr[xmlAttributeKey('alignmentHorizontal')] = store.alignmentHorizontal.toString();
            attr[xmlAttributeKey('alignmentVertical')] = store.alignmentVertical.toString();
        }
        attr[xmlAttributeKey('lineSpace')] = store.lineSpace.toString();
        const input = [{ settings: [{ '#text': '' }], ':@': attr }];
        const options = {
            processEntities: false,
            format: false,
            ignoreAttributes: false,
            preserveOrder: true,
            suppressBooleanAttributes: true,
        };
        const builder = new XMLBuilder(options);
        return builder.build(input);
    }

    public async editor2engine(data: string, bExport: boolean) {
        this._init(true);
        const parser = new XMLParser(parserOptions);
        data = data.replace(/&nbsp;/g, ' ');
        data = data.replace(/<br>/g, '<br/>');
        // 将settings合并到xml数据中
        const settings = this.getSettingsXML(bExport);
        data = `${settings}${data}`;
        let xmlArray = parser.parse(data);
        let transformArray: Array<any> = [];
        await this.cloneVar(transformArray, xmlArray);
        // 转换为engine能识别到的格式
        let builder = new XMLBuilder(builderOptions);
        let ret = builder.build(transformArray);
        if (showLog) {
            console.log(chalk.red('Editor -> Engine: \n', ret));
        }
        return ret;
    }

    private _peelOffSettings(array: Array<any>) {
        const store = appStore();
        for (let i = 0; i < array.length;) {
            const item = array[i];
            if (item[AttributeFlag]) {
                const attrs = new AttributeParser(item[AttributeFlag]);
                if (item['settings']) {
                    // store.fixSize = attrs.parser('fixSize')
                    //     .toBool();
                    // store.fixWidth = attrs.parser('fixWidth')
                    //     .toInt();
                    // store.fixHeight = attrs.parser('fixHeight')
                    //     .toInt();
                    store.lineSpace = attrs.parser('lineSpace')
                        .toInt();
                    store.alignmentHorizontal = attrs.parser('alignmentHorizontal')
                        .toInt();
                    store.alignmentVertical = attrs.parser('alignmentVertical')
                        .toInt();

                    array.splice(i, 1);
                    continue;
                } else if (item[Tags.TagFont]) {
                    store.defaultFontSize = attrs.parser('size')
                        .toInt();
                }
            }
            i++;
        }
    }

    private _peelOffRootFont(xmlArray: Array<any>) {
        // 将根节点的font恢复到body上
        for (let i = 0; i < xmlArray.length;) {
            const item = xmlArray[i];
            if (item[AttributeFlag]) {
                const attrs = new AttributeParser(item[AttributeFlag]);
                if (item['font']) {
                    if (attrs.isValid('size')) {
                        const size = attrs.parser('size')
                            .toInt();
                        console.log(`editor->engine: font-size=${size}`);
                        EditorRangeInstance.setBaseFontSize(size);
                    }
                    // todo color: 一般不会发生这种情况
                    return item['font'];
                }
            }
            i++;
        }
        return xmlArray;
    }

    public async engine2editor(xml: string) {
        this._init(false);
        const parser = new XMLParser(parserOptions);
        let xmlArray = parser.parse(xml);
        this._peelOffSettings(xmlArray);
        xmlArray = this._peelOffRootFont(xmlArray);
        let transformArray: Array<any> = [];
        await this.cloneVar(transformArray, xmlArray);
        let builder = new XMLBuilder(builderOptions);
        const ret = builder.build(transformArray);
        if (showLog) {
            console.log(chalk.green('Engine -> Editor: ', ret));
        }
        return ret;
    }
}