import {defineStore} from 'pinia';
import {inject} from 'vue';
import ConstVar from '@/rich-text-editor/const';

export interface IAppStore {
    xml: string,
    fixSize: boolean,
    fixWidth: number,
    fixHeight: number,
    lineSpace: number,
    defaultFontSize: number,
    alignmentHorizontal: number,
    alignmentVertical: number,
    bgColor: string,
    debugBoard: boolean,
}

const appStore = defineStore('app', {
    state: (): IAppStore => {
        return {
            xml: '',
            fixSize: true,
            defaultFontSize: 20,
            fixWidth: 100,
            fixHeight: 100,
            lineSpace: 0,
            alignmentHorizontal: ConstVar.Alignment.Horizontal.Left,
            alignmentVertical: ConstVar.Alignment.Vertical.Top,
            bgColor: '#000000',
            debugBoard: true,
        };
    },
    actions: {
        changeLineSpace(val: number) {
            this.lineSpace = val;
            this._change();
        },
        _change() {
            // 通知更新
        },
    },
});
export default appStore;