
import {defineComponent, onMounted, provide, ref, createVNode, render, nextTick} from 'vue';
import ToolBar from './view/toolbar.vue';
import Panel from '@/core/Panel';
import Msg from '../message';
import appStore from './store';
import ConstVar from './const';
import Utils from '@/rich-text-editor/utils';
import {XmlTranslate} from './translate/index';
import EditorRangeInstance from '@/rich-text-editor/EditorRange';
import ImgHandler from '@/rich-text-editor/view/img-handler.vue';
import TLink from '@/rich-text-editor/view/link.vue';
import TPopper from '@/rich-text-editor/comp/popper.vue';
import popperStore from '@/rich-text-editor/store-poper';
// @ts-ignore
import source from 'raw-loader!./view/iframe.html';
import Const from './const';
import TDialog from '@/rich-text-editor/dialog/dialog.vue';
import {DialogOption} from '@/rich-text-editor/dialog/index';
import {XMLBuilder} from 'fast-xml-parser';
import tinycolor from 'tinycolor2';
import {colorToHex} from './util-color';
import Base64 from '@/rich-text-editor/base64';

export default defineComponent({
  name: 'xml-text-editor',
  components: {
    TDialog,
    ImgHandler,
    TPopper,
    TLink,
    ToolBar,
  },
  setup(props, { emit }) {
    const store = appStore();
    let panel: Panel;
    const frameSource = ref(source || '');
    const frame = ref<HTMLIFrameElement>();

    async function onContentChange(data: string) {
      let ret = await new XmlTranslate().editor2engine(data, false);


      panel.dispatch({
        panel: Msg.Main.Name, action: Msg.Main.MsgUpdateRichText,
        data: {
          xml: ret,
          // fixSize: store.fixSize,
          // fixWidth: store.fixWidth,
          // fixHeight: store.fixHeight,
          // lineSpace: store.lineSpace,
          // alignmentHorizontal: store.alignmentHorizontal,
          // alignmentVertical: store.alignmentVertical,
        },
      });
    }

    let preString = '';
    let curString = '';
    const editor = ref();
    onMounted(async () => {
      Utils.seepFrameEvent();
      await EditorRangeInstance.initWithFrame(frame.value!, {});

      setInterval(async () => {
        curString = EditorRangeInstance.getInnerHtml();
        if (curString.length !== preString.length || preString !== curString) {
          preString = curString;
          await onContentChange(curString);
        }
      }, 500);
      panel = new Panel(Msg.RichText.Name);
      let ret = await panel.init();
      if (ret !== 0) {
        console.error('panel init failed');
      }
      panel.messages[Msg.RichText.MsgUpdateRichText] = async (data: Record<string, any>) => {
        let { r, g, b } = data.bg;
        r *= 255;
        g *= 255;
        b *= 255;

        const color = colorToHex({ r, g, b });
        appStore().bgColor = color;
        EditorRangeInstance.setBgColor(color);
        const innerHTML = await new XmlTranslate().engine2editor(data.xml);
        EditorRangeInstance.setInnerHtml(innerHTML);
        const { debugBoard } = data;
        appStore().debugBoard = debugBoard;
      };
      panel.dispatch({panel: Msg.Main.Name, action: Msg.Main.MsgGetRichText});
    });
    const xml = ref(store.xml);
    provide(ConstVar.Provide.UpdateConfig, (data: any) => {
      onContentChange(curString);
    });
    provide(ConstVar.Provide.UpdateBgColor, (color: string) => {
      const { r, g, b, a } = tinycolor(color)
        .toRgb();
      panel.dispatch({panel: Msg.Main.Name, action: Msg.Main.MsgSetBgColor, data: { r, g, b, a }});
    });
    provide(ConstVar.Provide.SetRefBg, async (base64: string) => {
      const { width, height } = await Base64.getImageSize(base64);
      base64 = await Base64.compressBase64(base64, width, height);
      base64 = Base64.cutImageHead(base64);
      panel.dispatch({panel: Msg.Main.Name, action: Msg.Main.MsgSetBg, data: { data: base64, width, height }});
    });
    provide(ConstVar.Provide.UpdateDebugBoard, (show: boolean) => {
      panel.dispatch({panel: Msg.Main.Name, action: Msg.Main.MsgUpdateDebugBoard, data: { show }});
    });
    return {
      frame,
      frameSource,
      xml,
      editor,
    };
  },
});
